.head {
  position: fixed !important;
  width: 100% !important;
  padding-top: 1rem !important;
  padding-bottom: 1.2rem !important;
  padding-left: 2.2rem !important;
  padding-right: 1rem;
  z-index: 150 !important;
  background: linear-gradient(180deg, #746969, rgba(82, 77, 77, 0.5)) !important;
}

.topbar-container {
  display: flex !important;
  flex-direction: row !important;
  flex-wrap: nowrap !important;
  min-height: 66px !important;
}
.index-logo { 
  width: 4rem !important;
  height: 4rem !important;
}
.logoDesc {
  font-size: 1.5rem !important;
  font-weight: bold !important;
}
.num-2 {
  color: rgb(43, 204, 142) !important;
  font-size: x-large !important;
  font-family: monospace !important;
}
.social-container {
}
.IDO-Pre {
  background-color: rgb(38, 99, 80) !important;
}
.IDO-Pre:hover {
  background-color: rgb(96, 219, 204) !important;
}

.baseChart {
  min-width: 3rem !important;
  display: flex !important;
  justify-content: center !important;
  align-items: center !important;
  color: antiquewhite !important;
}
.baseChart:hover {
  color: rgb(107, 74, 92) !important;
}


@media only screen and (max-width: 920px) {

  .index-logo { 
    width: 3.5rem !important;
    height: 3.5rem !important;
  }
  .logoDesc {
    font-size: 1rem !important;
  }

}

@media only screen and (max-width: 550px) { 

  .head {
    padding-left: 1rem !important;
  }

  .language { 
    padding-right: 0 !important;
  }
  .language-btn { 
    padding: 0.2rem !important;
  }
  .index-logo { 
    width: 2.5rem !important;
    height: 2.5rem !important;
  }
  .index-IDO { 
    width: 100% !important;
  }
  .index-IDOBtn { 
    padding: 0.2rem !important;
    width: 100% !important;
  }

}
